@import "reset.css";

div,
label,
input {
  box-sizing: border-box;
}

@font-face {
  font-family: "Gyuri";
  src: url(../fonts/나눔손글씨규리의일기.ttf) format("truetype");
}

html {
  font-size: 10px;
}

.App {
  background: #fff9c4;
  width: 100vw;
  height: 100vh;
  min-height: 900px;
  display: flex;
  font-family: "Gyuri";
  user-select: none;
}

.container {
  width: 550px;
  height: 600px;
  background: white;
  margin: auto;
  padding: 24px;
  position: relative;
}

.header {
  font-size: 4rem;
  margin: 20px;
  h1 i {
    color: #cbc693;
    margin-right: 5px;
  }
}

.date {
  margin: 24px 0;
  font-size: 2rem;
  font-weight: 700;
  text-align: left;
}

.list {
  font-size: 2.5rem;
  display: flex;
  padding: 0 30px;
  align-items: center;
  margin-bottom: 20px;
  p {
    padding: 0 10px;
    font-weight: 700;
  }
  label {
    width: 15px;
    height: 15px;
    border: 2px solid black;
    position: relative;
    cursor: pointer;

    &::after {
      content: "✔";
      font-size: 2.2rem;
      position: absolute;
      left: -2px;
      top: -8px;
      right: 0;
      bottom: 0px;
      display: none;
    }
  }
  .checked::after {
    display: block;
  }
}

.checked + p {
  color: black;
  text-decoration: line-through;
}

.delete {
  margin-left: auto;
  width: 20px;
  height: 20px;
  cursor: pointer;
  font-size: 2rem;
  transform: rotate(45deg);
  background-color: #ba000d;
  border-radius: 50%;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer {
  width: 91%;
  display: flex;
  justify-content: space-between;
  position: absolute;
  bottom: 60px;
  p {
    font-size: 2rem;
  }
}

.add {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 50%;
  background: #0069c0;
  cursor: pointer;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
}

.clear {
  @extend .add;
  background: green;
}

.modal {
  width: 100vw;
  height: 100vh;
  position: fixed;
  background: rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  font-size: 3rem;
  font-weight: 700;
}

.box {
  width: 30%;
  min-width: 300px;
  height: 30%;
  min-height: 300px;
  background: white;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  input[type="text"] {
    height: 40px;
    width: 100%;
    border: 2px solid gray;
    border-radius: 5px;
    margin-top: 20px;
    font-size: 1.7rem;
  }
  input[type="text"]:focus {
    outline: none;
    border: 2px solid #0069c0;
    transition: border 0.4s ease-in-out;
  }
}

.add-text {
  width: 60%;
  .btn {
    width: 50px;
    height: 30px;
    margin: 15px;
    border: none;
    font-weight: 700;
    cursor: pointer;
    border-radius: 5px;
    color: white;
  }
  .btn-submit {
    background: #0069c0;
  }
  .btn-close {
    background: #ba000d;
  }
  .warn {
    font-size: 2.3rem;
    color: #ba000d;
  }
}

@media screen and(max-width: 576px) {
  .footer {
    justify-content: space-around;
  }
}
